@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700,900&display=swap);
@keyframes fadeInLeft {
0% { transform: translateX(200%);
}
100% { transform: translateX(0%);
}
}
@keyframes fadeInRight {
0% { transform: translateX(-200%);
}
100% { transform: translateX(0%);
}
}
.toast-bottom-left .toast,
.toast-top-left .toast {
    animation: fadeInRight 0.3s ease-in-out forwards;
    transform: translateX(-200%);
}
.toast-bottom-right .toast,
.toast-top-right .toast {
    animation: fadeInLeft 0.3s ease-in-out forwards;
    transform: translateX(200%);
}
.toast-bottom-left .toast:hover,
.toast-top-left .toast:hover {
    margin-left: -4px;
}
.toast-bottom-right .toast:hover,
.toast-top-right .toast:hover {
    margin-right: -4px;
}
.toast:hover {
    opacity: .5;
}
.toast-top-full-width .toast,
.toast-bottom-full-width .toast {
    border-radius: 0px !important;
}
.toast .left {
    margin: 0 !important;
    padding: 15px !important;
}
.toast .right {
    background: #fff !important;
    color: #172b43 !important;
    padding: 15px !important;
    -ms-flex-positive: 1 !important;
        flex-grow: 1 !important;
}
.toast .toast-title {
    font-size: 16px;
}
.toast .toast-message {
    font-size: 15px;
}
.toast.toast-success {
    background-color: #4CAF50;
}
.toast.toast-success .toast-title {
    color: #4CAF50;
}
.toast.toast-error {
    background-color: #F44336;
}
.toast.toast-error .toast-title {
    color: #F44336;
}
.toast.toast-info {
    background-color: #172b43;
}
.toast.toast-info .toast-title {
    color: #172b43;
}
.toast.toast-warning {
    background-color: #FFA000;
}
.toast.toast-warning .toast-title {
    color: #FFA000;
}
.fa-secondary {
    opacity: 0.4;
}
.toast {
    font-family: 'Roboto', sans-serif !important;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, .15);
    display: -ms-flexbox;
    display: flex;
    padding: 0px !important;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    transition: .3s ease all;
}
.left {
    -ms-flex: 0 0 30px;
        flex: 0 0 30px;
    margin-right: 5px;
    -ms-flex-item-align: center;
        -ms-grid-row-align: center;
        align-self: center;
}
.left svg {
    width: 30px;
    height: 30px;
}
.left svg path {
    fill: #fff;
}
.toast-title {
    font-weight: 600;
}
.toast-message {
    -ms-word-wrap: break-word;
    word-wrap: break-word;
    font-weight: 100;
    margin-top: 4px;
    line-height: 1.2;
}
.toast-message a,
.toast-message label {
    color: #FFFFFF;
}
.toast-message a:hover {
    color: #CCCCCC;
    text-decoration: none;
}
.toast-close-button {
    position: relative;
    right: -0.3em;
    top: -0.3em;
    float: right;
    font-size: 20px;
    font-weight: bold;
    color: #FFFFFF;
}
.toast-close-button:hover,
.toast-close-button:focus {
    color: #172b43;
    text-decoration: none;
    cursor: pointer;
}
/*Additional properties for button version
 iOS requires the button element instead of an anchor tag.
 If you want the anchor version, it requires `href="#"`.*/
button.toast-close-button {
    padding: 0;
    cursor: pointer;
    background: transparent;
    border: 0;
    -webkit-appearance: none;
}
.toast-top-center {
    top: 6px;
    right: 0;
    width: 100%;
}
.toast-bottom-center {
    bottom: 0;
    right: 0;
    width: 100%;
}
.toast-top-full-width {
    top: 0;
    right: 0;
    width: 100%;
}
.toast-bottom-full-width {
    bottom: -6px;
    right: 0;
    width: 100%;
}
.toast-top-left {
    top: 12px;
    left: 12px;
}
.toast-top-right {
    top: 12px;
    right: 12px;
}
.toast-bottom-right {
    right: 12px;
    bottom: 2px;
}
.toast-bottom-left {
    bottom: 2px;
    left: 12px;
}
.toast-container {
    position: fixed;
    z-index: 999999;
    pointer-events: none;
    transition: 3s ease all;
    /*overrides*/
}
.toast-container * {
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
}
.toast-container > div {
    position: relative;
    pointer-events: auto;
    overflow: hidden;
    margin: 0 0 10px;
    padding: 15px;
    width: 350px;
    border-radius: 3px 3px 3px 3px;
    -moz-border-radius: 3px 3px 3px 3px;
    -webkit-border-radius: 3px 3px 3px 3px;
    background-position: 15px center;
    background-repeat: no-repeat;
    color: #FFFFFF;
}
.toast-container > :hover {
    cursor: pointer;
}
